body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

table {
  width: 75%;
  background: #e1f2e4;
  margin: 0 auto;
  margin-top: 65px;
  margin-bottom: 15px;
  border-radius: 5px;
  border-collapse: collapse;
}

.table-header {
  font-size: 0.920em;
  color: #fff;
  background: #006400;
}

.table-col-1 {
  width: 10%;
}
.table-header-col-2 {
  width: 25%;
}
.table-col-3 {
  width: 40%;
}
tr {
  margin-bottom: 10px;
}
th {
  height: 40px;
}

footer a {
  color: #fff;
  text-decoration: none;
}



